import React, { useState } from 'react';
import { Drawer, Switch, IconButton, List, ListItem } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';

const Menu: React.FC = (
  {

  }, {}) => {
  const [menuToggle, setMenuToggle] = useState(false);

  const toggleDrawer = () => {
    setMenuToggle(!menuToggle);
  };

  return (
    <>
      <IconButton
        color="inherit"
        aria-label="open drawer"
        edge="end"
        onClick={() => toggleDrawer()}
      />
      <Drawer
        anchor = {'left'}
        open = {menuToggle}
        onClose = {() => setMenuToggle(false)}
      >
        <div>
          <List>
            <ListItem>
              Theme Mode
            </ListItem>
          </List>
          <Divider />
          <List component="nav" aria-label="secondary mailbox folders">
            <ListItem button onClick={() => console.log('toeic1')}>
              <ListItemText primary="toeic1"/>
            </ListItem>
            <ListItem button onClick={() => console.log('toeic2')}>
              <ListItemText primary="toeic2" />
            </ListItem>
          </List>
        </div>
      </Drawer>
      <MenuIcon />
    </>
  );
}

export default Menu;