import * as React from 'react';
import { Grid } from '@material-ui/core';

interface PartProps {
  currentPart: string;
  changeCurrentPart: (val:string) => void;
  slides: {
    part: string;
    word: string;
    partOfSpeech: string;
    trans: string;
    sentence: string;
    sentenceTrans: string;
  }[];
}

class Part extends React.Component<PartProps, {}>{
  render () {
    const slides = this.props.slides;
    const parts = slides.reduce((val:string[], cur) => {
      if (!val.includes(cur.part)) {
        val.push(cur.part);
      }
      return val;
    }, []);

    return (
      <Grid
        container
        direction="row"
        justify="space-around"
        alignItems="flex-end"
      >
        <ul>
          {
            parts.map(p => {
              return(
                <li key={p}>
                  <button 
                    onClick={() => this.props.changeCurrentPart(p)}
                  >
                    {p}{p === this.props.currentPart ? '(current)': ''}
                  </button>
                </li>
              );
            })
          }
        </ul>
      </Grid>
    );
  }
}

export default Part;